
#questionnaire img {
  width: 100%;
}
#questionnaire .borderTop {
  border-top: none !important;
}
#questionnaire .mask {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  z-index: 99;
}
#questionnaire .mask .box {
    position: fixed;
    width: 70%;
    background: #fff;
    top: 20%;
    height: 50%;
    margin-left: 15%;
    border-radius: 8px;
}
#questionnaire .mask .box p {
      font-size: 18px;
      line-height: 30px;
}
#questionnaire .mask .box .naire_words {
      position: absolute;
      width: 100%;
      text-align: center;
      font-size: 18px;
      color: #101010;
      top: 65%;
}
#questionnaire .mask .box .btn {
      border: 1px solid #dddddd;
      display: inline-block;
      padding: 10px 20px;
      border-radius: 8px;
}
#questionnaire .van-radio, #questionnaire .van-checkbox.van-checkbox--round {
  display: -webkit-box;
  display: flex;
  display: -webkit-flex;
}
#questionnaire .van-radio .van-checkbox__input, #questionnaire .van-radio .van-radio__input, #questionnaire .van-checkbox.van-checkbox--round .van-checkbox__input, #questionnaire .van-checkbox.van-checkbox--round .van-radio__input {
    min-width: 20px;
}
#questionnaire .onClass {
  color: #409EFF;
}
#questionnaire .questionnaireTitle {
  height: 3rem;
  overflow: hidden;
}
#questionnaire .questionnaireTitle div {
    padding: .3rem;
    line-height: .4rem;
    color: #FFFFFF;
    position: absolute;
    bottom: 0;
    left: 0;
}
#questionnaire .msg {
  text-align: center;
  color: #949494;
  margin-top: 10px;
}
#questionnaire .exercise {
  /*width: 96%;*/
  /*margin: 10px auto;*/
  /*box-shadow: 0 0 5px 0 #aaaaaa;*/
  /*border-radius: 8px;*/
  background-color: #FFFFFF;
  padding: .2rem;
}
#questionnaire .exercise .subject {
    border-top: 1px solid #ebebeb;
    padding-top: 15px;
    padding-bottom: 5px;
}
#questionnaire .exercise .subject .subjectTitle {
      margin-left: .5rem;
      color: #6c6c6c;
      line-height: 25px;
}
#questionnaire .exercise .subject .subjectTitle .subjectB {
        margin-top: 20px;
}
#questionnaire .exercise .subject .subjectTitle .subjectB .van-radio .van-radio__input {
          margin-top: 3px;
}
#questionnaire .exercise .subject .subjectTitle .subjectB .van-radio .van-radio__label {
          line-height: 28px;
}
#questionnaire .exercise .subject .subjectTitle .subjectB .van-checkbox .van-checkbox__input {
          margin-top: 4px;
}
#questionnaire .exercise .subject .subjectTitle .subjectB .van-checkbox .van-checkbox__label {
          line-height: 28px;
}
#questionnaire .exercise .contents {
    text-align: center;
    margin-bottom: .2rem;
    background: #ffffff;
}
#questionnaire .exercise .contents .van-cell.van-hairline.van-field .van-cell__value {
      padding-left: 0;
}
#questionnaire .exercise .contents .van-cell-group {
      padding-left: 0;
}
#questionnaire .exercise .contents .van-cell-group .van-field--has-textarea {
        background: #F3F9FF;
        color: #6c6c6c;
}
#questionnaire .exercise .contents .van-cell-group .van-field--has-textarea .van-field__control {
          margin-left: .2rem;
          background: #F3F9FF;
          min-height: 100px;
}



