
#questionnaire img {
  width: 100%;
}
#questionnaire .borderTop {
  border-top: none !important;
}
#questionnaire .mask {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  z-index: 99;
}
#questionnaire .mask .box {
    position: fixed;
    width: 70%;
    background: #fff;
    top: 25%;
    margin-left: 15%;
    border-radius: 8px;
}
#questionnaire .mask .box p {
      font-size: 18px;
      line-height: 30px;
}
#questionnaire .mask .box .words {
      position: absolute;
      text-align: center;
      font-size: 18px;
      color: #101010;
      top: 70%;
      left: 23%;
}
#questionnaire .van-radio, #questionnaire .van-checkbox.van-checkbox--round {
  display: -webkit-box;
  display: flex;
  display: -webkit-flex;
}
#questionnaire .van-radio .van-checkbox__input, #questionnaire .van-radio .van-radio__input, #questionnaire .van-checkbox.van-checkbox--round .van-checkbox__input, #questionnaire .van-checkbox.van-checkbox--round .van-radio__input {
    min-width: 20px;
}
#questionnaire .onClass {
  color: #409EFF;
}
#questionnaire .questionnaireTitle {
  height: 3rem;
  overflow: hidden;
}
#questionnaire .questionnaireTitle div {
    padding: .3rem;
    line-height: .4rem;
    color: #FFFFFF;
    position: absolute;
    bottom: 0;
    left: 0;
}
#questionnaire .msg {
  text-align: center;
  color: #949494;
  margin-top: 10px;
}
#questionnaire .exercise {
  /*width: 96%;*/
  /*margin: 10px auto;*/
  /*box-shadow: 0 0 5px 0 #aaaaaa;*/
  /*border-radius: 8px;*/
  background-color: #FFFFFF;
  padding: .2rem;
  padding-bottom: 50px;
}
#questionnaire .exercise .subject {
    border-top: 1px solid #dfe6fb;
    padding-top: 15px;
    padding-bottom: 5px;
}
#questionnaire .exercise .subject .subjectTitle {
      margin-left: .5rem;
}
#questionnaire .exercise .subject .subjectTitle .subjectA {
        line-height: .4rem;
        margin: .2rem 0;
}
#questionnaire .exercise .contents {
    text-align: center;
    margin-bottom: .2rem;
    background: #ffffff;
}
#questionnaire .exercise .contents .van-cell.van-hairline.van-field .van-cell__value {
      padding-left: 0;
}
#questionnaire .exercise .contents .van-cell-group {
      padding-left: 0;
}
#questionnaire .exercise .contents .van-cell-group .van-field--has-textarea {
        background: #F3F9FF;
}
#questionnaire .exercise .contents .van-cell-group .van-field--has-textarea .van-field__control {
          margin-left: .2rem;
          background: #F3F9FF;
          min-height: 100px;
}
#questionnaire .container {
  background: #fff;
  color: #999;
  width: 100%;
  height: 100%;
  text-align: center;
  position: absolute;
}
#questionnaire .container .content .content_img {
    position: relative;
    margin-top: 100px;
}
#questionnaire .container .content .title {
    position: relative;
    margin-top: 20px;
    font-size: 25px;
    color: #39b1ff;
}
#questionnaire .container .content .last_title {
    position: relative;
    margin-top: 30px;
    font-size: 14px;
}



