
#commentOn .showPopupTitle, #commentOn .showPopupFooter {
  text-align: center;
  padding: .3rem 0;
  font-size: .36rem;
}
#commentOn .showPopupTitle {
  border-bottom: 1px solid #f4f4f4;
}
#commentOn .showPopupFooter {
  border-top: 1px solid #f4f4f4;
  color: #409EFF;
}
#commentOn .showPopup {
  width: 6.6rem;
  padding-left: .3rem;
  max-height: 8rem;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
#commentOn header {
  padding: .3rem;
  background: #FFFFFF;
}
#commentOn header .address {
    font-size: .4rem;
    text-align: center;
}
#commentOn header .marking {
    margin: .3rem 0;
}
#commentOn header .marking div {
      display: -webkit-box;
      display: flex;
      display: -webkit-flex;
      -webkit-box-pack: center;
      -webkit-justify-content: center;
              justify-content: center;
      -webkit-box-align: center;
      -webkit-align-items: center;
              align-items: center;
}
#commentOn header .marking div p {
        margin: 0 .1rem;
}
#commentOn header .marking div p i {
          font-size: .5rem;
          color: #FFD000;
}
#commentOn header .marking div:first-of-type {
      margin-bottom: .24rem;
}
#commentOn .contents, #commentOn .pic {
  background: #ffffff;
}
#commentOn .contents .van-cell.van-hairline.van-field .van-cell__value {
  padding-left: 0;
}
#commentOn .contents {
  text-align: center;
  margin-bottom: .2rem;
}
#commentOn .contents .van-cell-group .van-field--has-textarea .van-field__control {
    min-height: 150px;
}
#commentOn .pic {
  padding: .1rem .2rem .3rem;
  background: #fff;
  width: 100%;
}
#commentOn .pic .title {
    font-size: .36rem;
    padding: .26rem .4rem;
}



