
#quality {
  overflow: hidden;
}
#quality .unit {
    display: -webkit-box;
    display: flex;
    display: -webkit-flex;
}
#quality .unit b {
      padding: 0 .3rem;
      height: 44px;
      line-height: 44px;
      border-bottom: 1px solid #F4F4F4;
}
#quality .titleSwitch {
    background: #FFFFFF;
    border-bottom: 1px solid #F4F4F4;
}
#quality .titleSwitch .cellGroup {
      display: -webkit-box;
      display: flex;
      display: -webkit-flex;
      -webkit-box-align: center;
      -webkit-align-items: center;
              align-items: center;
      -webkit-box-pack: justify;
      -webkit-justify-content: space-between;
              justify-content: space-between;
}
#quality .titleSwitch .cellGroup .van-cell {
        width: 66px;
}
#quality .titleSwitch .cellGroup .cellA {
        display: -webkit-box;
        display: flex;
        display: -webkit-flex;
        -webkit-box-align: center;
        -webkit-align-items: center;
                align-items: center;
        margin-left: -7px;
}
#quality .titleSwitch .cellGroup .cellA .requiredIcon {
          color: #f44;
}



