
#container {
  padding: 0 .1rem;
}
#container .pickfiles {
    display: -webkit-box;
    display: flex;
    display: -webkit-flex;
    /* Safari */
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
#container .pickfiles > div {
      margin-left: .3rem;
      margin-top: .3rem;
}
#container .pickfiles .upButton {
      width: 1.5rem;
      height: 1.5rem;
      background: #f6f6f6;
      text-align: center;
      line-height: 1.5rem;
}
#container .pickfiles .upButton .plus {
        font-size: 1rem;
        color: #aaa;
}
#container .pickfiles .progress {
      width: 100%;
      position: absolute;
      bottom: .5rem;
      font-size: .5rem;
      text-align: center;
}
#container .pickfiles .remove {
      text-align: center;
      width: .5rem;
      height: .5rem;
      line-height: .5rem;
      border-radius: 50%;
      position: absolute;
      top: -.2rem;
      right: -.2rem;
      z-index: 1;
      background: #333;
      color: #fff;
      font-size: .5rem;
}



