
#switchCraft .btnBox {
  position: fixed;
  bottom: 1.5rem;
  right: 16px;
  z-index: 10;
  width: 50px;
  height: 50px;
  line-height: 48px;
  border-radius: 50%;
  border: none;
  outline: none;
  color: #fff;
  background: #409EFF;
}
#switchCraft .btnBox i {
    display: inline-block;
    margin-left: 2px;
    font-size: 30px;
}
#switchCraft .btnI {
  line-height: 45px;
}
#switchCraft .btnI i {
    margin-left: 1px;
    font-size: 26px;
}
#switchCraft .menu {
  position: fixed;
  bottom: 1.5rem;
  right: 16px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  -webkit-transition: all .7s ease-in;
  transition: all .7s ease-in;
}
#switchCraft .menu.move-enter-active .inner {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    -webkit-transition-timing-function: cubic-bezier(0, 0.57, 0.44, 1.97);
            transition-timing-function: cubic-bezier(0, 0.57, 0.44, 1.97);
}
#switchCraft .menu.move-enter-active .inner-1 {
    -webkit-transition-delay: .1s;
            transition-delay: .1s;
}
#switchCraft .menu.move-enter-active .inner-2 {
    -webkit-transition-delay: .2s;
            transition-delay: .2s;
}
#switchCraft .menu.move-enter-active .inner-3 {
    -webkit-transition-delay: .3s;
            transition-delay: .3s;
}
#switchCraft .menu.move-enter .inner, #switchCraft .menu.move-leave-active .inner {
    -webkit-transition-timing-function: ease-in-out;
            transition-timing-function: ease-in-out;
}
#switchCraft .menu.move-enter .inner-1, #switchCraft .menu.move-leave-active .inner-1 {
    -webkit-transform: translate3d(0, 60px, 0);
            transform: translate3d(0, 60px, 0);
    -webkit-transition-delay: .3s;
            transition-delay: .3s;
}
#switchCraft .menu.move-enter .inner-2, #switchCraft .menu.move-leave-active .inner-2 {
    -webkit-transform: translate3d(40px, 40px, 0);
            transform: translate3d(40px, 40px, 0);
    -webkit-transition-delay: .2s;
            transition-delay: .2s;
}
#switchCraft .menu.move-enter .inner-3, #switchCraft .menu.move-leave-active .inner-3 {
    -webkit-transform: translate3d(60px, 0, 0);
            transform: translate3d(60px, 0, 0);
    -webkit-transition-delay: .1s;
            transition-delay: .1s;
}
#switchCraft .menu .inner {
    display: inline-block;
    position: absolute;
    width: 34px;
    height: 34px;
    line-height: 34px;
    border-radius: 50%;
    background: #F19851;
    text-align: center;
    color: #fff;
    -webkit-transition: all .4s;
    transition: all .4s;
}
#switchCraft .menu .inner-1 {
    top: -50px;
    left: 10px;
}
#switchCraft .menu .inner-2 {
    left: -30px;
    top: -30px;
    background: #DDDDDD;
}
#switchCraft .menu .inner-3 {
    left: -50px;
    top: 10px;
    background: #DDDDDD;
}



