
#commentOn .contents, #commentOn .pic {
  background: #ffffff;
}
#commentOn .van-cell.van-hairline.van-field .van-cell__value {
  padding-left: 0;
}
#commentOn .contents {
  margin-bottom: .2rem;
}
#commentOn .contents .van-cell-group .van-field--has-textarea .van-field__control {
    min-height: 150px;
}
#commentOn .pic {
  padding: .1rem .2rem .3rem;
  background: #fff;
  width: 100%;
}
#commentOn .pic .title {
    font-size: .36rem;
    padding: .26rem .4rem;
}



