
#exam img {
  width: 100%;
}
#exam .borderTop {
  border-top: none !important;
}
#exam .mask {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  z-index: 99;
}
#exam .mask .box {
    position: fixed;
    width: 70%;
    background: #fff;
    top: 25%;
    margin-left: 15%;
    border-radius: 8px;
}
#exam .mask .box p {
      font-size: 18px;
      line-height: 30px;
}
#exam .mask .box .words {
      position: absolute;
      text-align: center;
      font-size: 18px;
      color: #101010;
      top: 70%;
      left: 23%;
}
#exam .van-radio, #exam .van-checkbox.van-checkbox--round {
  display: -webkit-box;
  display: flex;
  display: -webkit-flex;
}
#exam .van-radio .van-checkbox__input, #exam .van-radio .van-radio__input, #exam .van-checkbox.van-checkbox--round .van-checkbox__input, #exam .van-checkbox.van-checkbox--round .van-radio__input {
    min-width: 20px;
}
#exam .onClass {
  color: #409EFF;
}
#exam .examTitle {
  margin-top: 20px;
  background: #fff;
  height: 3rem;
}
#exam .examTitle .key {
    padding: 15px 0px;
    margin-bottom: 10px;
    line-height: .4rem;
    position: absolute;
    left: 20px;
    color: #9c9c9c;
    font-size: 14px;
    width: 90%;
}
#exam .examTitle .key .value {
      margin-left: 10px;
      color: #6c6c6c;
      font-size: 16px;
}
#exam .examTitle .import_questions {
    text-align: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 75px;
    border-radius: 5px;
    color: #9c9c9c;
}
#exam .examTitle .import_questions .import_left {
      width: 90%;
      height: 25px;
      line-height: 25px;
      margin: 0 auto;
}
#exam .msg {
  text-align: center;
  color: #949494;
  margin-top: 10px;
}
#exam .exercise {
  margin-top: 20px;
  background-color: #FFFFFF;
  padding: .2rem;
  padding-bottom: 50px;
}
#exam .exercise .subject {
    border-top: 1px solid #dfe6fb;
    padding-top: 15px;
    padding-bottom: 5px;
}
#exam .exercise .subject .subjectTitle {
      margin-left: .5rem;
}
#exam .exercise .subject .subjectTitle .subjectA {
        line-height: .4rem;
        margin: .2rem 0;
}
#exam .exercise .contents {
    text-align: center;
    margin-bottom: .2rem;
    background: #ffffff;
}
#exam .exercise .contents .van-cell.van-hairline.van-field .van-cell__value {
      padding-left: 0;
}
#exam .exercise .contents .van-cell-group {
      padding-left: 0;
}
#exam .exercise .contents .van-cell-group .van-field--has-textarea {
        background: #F3F9FF;
}
#exam .exercise .contents .van-cell-group .van-field--has-textarea .van-field__control {
          margin-left: .2rem;
          background: #F3F9FF;
          min-height: 100px;
}



