
#exam img {
  width: 100%;
}
#exam .border_col {
  /*border: 1px solid #39b1ff;*/
  /*height: 100px;*/
}
#exam .boxHeight {
  height: 200px !important;
}
#exam .count_down {
  position: relative !important;
  width: initial !important;
  top: 100px;
  float: right;
  margin-right: 40px;
}
#exam .force {
  position: absolute;
  text-align: center;
  font-size: 14px;
  top: 86%;
  left: 13%;
  color: #aaaaaa;
  width: 75%;
  line-height: 18px;
}
#exam .van-cell:not(:last-child)::after {
  border: none;
}
#exam .van-hairline--bottom::after, #exam .van-hairline--left::after,
#exam .van-hairline--right::after,
#exam .van-hairline--surround::after,
#exam .van-hairline--top-bottom::after,
#exam .van-hairline--top::after,
#exam .van-hairline::after {
  border: none;
}
#exam .borderTop {
  border-top: none !important;
}
#exam .mask {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  z-index: 99;
}
#exam .mask .box {
    position: fixed;
    width: 70%;
    background: #fff;
    top: 25%;
    margin-left: 15%;
    border-radius: 8px;
}
#exam .mask .box p {
      font-size: 18px;
      line-height: 30px;
}
#exam .mask .box .exam_words {
      position: absolute;
      text-align: center;
      font-size: 18px;
      color: #101010;
      width: 100%;
      top: 60%;
}
#exam .mask .box .btn {
      border: 1px solid #dddddd;
      display: inline-block;
      padding: 10px 20px;
      border-radius: 8px;
}
#exam .van-radio, #exam .van-checkbox.van-checkbox--round {
  display: -webkit-box;
  display: flex;
  display: -webkit-flex;
}
#exam .van-radio .van-checkbox__input, #exam .van-radio .van-radio__input, #exam .van-checkbox.van-checkbox--round .van-checkbox__input, #exam .van-checkbox.van-checkbox--round .van-radio__input {
    min-width: 20px;
}
#exam .onClass {
  color: #409EFF;
}
#exam .examTitle {
  margin-top: 20px;
  background: #fff;
  height: 3rem;
}
#exam .examTitle .key {
    padding: 15px 0px;
    margin-bottom: 10px;
    line-height: .4rem;
    position: absolute;
    left: 20px;
    color: #9c9c9c;
    font-size: 14px;
    width: 90%;
}
#exam .examTitle .key .value {
      margin-left: 10px;
      color: #6c6c6c;
      font-size: 16px;
}
#exam .examTitle .import_questions {
    text-align: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 75px;
    border-radius: 5px;
}
#exam .examTitle .import_questions .import_left {
      width: 90%;
      height: 25px;
      line-height: 25px;
      margin: 0 auto;
}
#exam .msg {
  text-align: center;
  color: #949494;
  margin-top: 10px;
}
#exam .exercise {
  /*width: 96%;*/
  /*margin: 10px auto;*/
  /*box-shadow: 0 0 5px 0 #aaaaaa;*/
  /*border-radius: 8px;*/
  margin-top: 20px;
  background-color: #FFFFFF;
  padding: .2rem;
}
#exam .exercise .subject {
    border-top: 1px solid #ebebeb;
    padding-top: 15px;
    padding-bottom: 5px;
}
#exam .exercise .subject .subjectTitle {
      margin-left: .5rem;
      color: #6c6c6c;
      line-height: 25px;
}
#exam .exercise .subject .subjectTitle .subjectB .van-radio .van-radio__input {
        margin-top: 3px;
}
#exam .exercise .subject .subjectTitle .subjectB .van-radio .van-radio__label {
        line-height: 25px;
}
#exam .exercise .subject .subjectTitle .subjectB .van-checkbox {
        height: 25px !important;
}
#exam .exercise .subject .subjectTitle .subjectB .van-checkbox .van-checkbox__input {
          margin-top: 4px;
}
#exam .exercise .subject .subjectTitle .subjectB .van-checkbox .van-checkbox__label {
          line-height: 25px;
}
#exam .exercise .content {
    text-align: center;
    margin-bottom: .2rem;
}
#exam .exercise .content .van-cell.van-hairline.van-field .van-cell__value {
      padding-left: 0;
}
#exam .exercise .content ::-webkit-input-placeholder {
      /* WebKit, Blink, Edge */
      color: #dddee0;
}
#exam .exercise .content :-moz-placeholder {
      /* Mozilla Firefox 4 to 18 */
      color: #dddee0;
}
#exam .exercise .content ::-moz-placeholder {
      /* Mozilla Firefox 19+ */
      color: #dddee0;
}
#exam .exercise .content :-ms-input-placeholder {
      /* Internet Explorer 10-11 */
      color: #dddee0;
}
#exam .exercise .content .van-cell-group {
      padding-left: 0;
}
#exam .exercise .content .van-cell-group .van-cell__value {
        background: #F3F9FF;
        border-radius: 5px;
}
#exam .exercise .content .van-cell-group .van-cell__value input {
          background: #F3F9FF;
          padding: 10px 15px;
          color: #6c6c6c;
}
#exam .exercise .content .van-cell-group .van-cell__value .van-field__icon {
          color: #999;
          margin-right: 15px !important;
}
#exam .exercise .content .van-cell-group .van-field--has-textarea {
        background: #F3F9FF;
        border-radius: 5px;
        color: #6c6c6c;
}
#exam .exercise .content .van-cell-group .van-field--has-textarea .van-field__control {
          margin-left: .2rem;
          background: #F3F9FF;
          min-height: 100px;
}
#exam .exercise .content .van-cell-group .van-field--has-textarea .van-field__icon {
          margin-right: 0 !important;
}



